



















































import Vue from "vue";

export default Vue.extend({
    name: "PageNavigationItem",

    props: ["name", "type", "pageId", "visible"],

    computed: {
        contentClass() {
            return this.type === "child" ? "pt-0 pb-0" : "";
        },
        circleClass() {
            const typeClass = this.type === "child" ? "bg-info" : "bg-warning";
            return `circle mr-2 ${typeClass}`;
        },
    },
});
