












































































import Vue from "vue";
import Page from "../models/page";
import PageNavigationItem from "@/components/PageNavigationItem.vue";
import PageHeader from "@/components/PageHeader.vue";
import FilterContainer from "@/components/FilterContainer.vue";
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import EmptyListInfo from "@/components/EmptyListInfo.vue";

export default Vue.extend({
    name: "Pages",

    components: {
        EmptyListInfo,
        FilterContainer,
        LoadingIndicator,
        PageNavigationItem,
        PageHeader,
    },

    data() {
        return {
            isLoading: true,
            showAll: false,
            activePage: 1,
        };
    },

    computed: {
        navigation() {
            return this.$store.state.navigation.all;
        },
        filters() {
            const filters = {
                page: this.activePage,
                showAll: this.showAll,
            };

            return filters;
        },
        resultsCount() {
            return this.navigation ? this.navigation.total : 0;
        },
    },

    created() {
        this.loadNavigation();
    },

    methods: {
        loadNavigation() {
            this.isLoading = true;
            this.$store
                .dispatch("navigation/loadAll", this.filters)
                .catch(() => {
                    this.$snotify.error(
                        "Die Navigation konnten nicht geladen werden"
                    );
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
        editPage(page: Page) {
            this.$router.push({
                name: "pages-edit",
                params: { pageId: page.id },
            });
        },
        deletePage(page: Page) {
            if (window.confirm("Soll die Seite wirklich gelöscht werden?")) {
                this.$store
                    .dispatch("pages/delete", page)
                    .then(() => {
                        this.$snotify.success("Die Seite wurde gelöscht!");
                        this.loadNavigation();
                    })
                    .catch(() => {
                        this.$snotify.error(
                            "Die Seite konnte nicht gelöscht werden!"
                        );
                    });
            }
        },
        updateShowAll(showAll) {
            this.showAll = showAll;
            this.loadNavigation();
        },
        updateActivePage(page) {
            this.activePage = page;
            this.loadNavigation();
        },
    },
});
